body {
  font-family:'Cormorant Garamond', serif;
  background-image: url(https://i.pinimg.com/originals/6a/a3/7f/6aa37fd0017bdb291ca8cbdd8b0ede52.gif);
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-color: rgba(21, 73, 0, 0.8);
  color: white;
}
html {
  scroll-behavior: smooth;
}

.main-container {
  display: flex;
  flex-direction: column;
}

.navbar{
  background-color: rgba(15, 36, 3, 0.8);
  padding: 10px;
  position: sticky;
  top:0;
  z-index: 10;
  display: flex;
  justify-content: center;
  border-bottom: 3px solid #6bcf67;
}

.logo {
  background-color: rgba(15, 36, 3, 0.8);
  font-size: 45px;
  padding: 5px;
  padding-right: 25px;
  display: flex;
  justify-content: center;
}

.nav-links a {
  margin-right: 20px;
  color: #ccc;
  text-decoration: none;
  transition: color 0.3s;
  font-size: 20px;
}

.nav-links a:hover {
  color: white;
}

.section {
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;  
}

.home-image {
  width: auto;
  height: 700px;
  display: block;
  margin: 0 auto;
  transition: transform 0.3s ease-in-out;
}

.home-image:hover {
  transform: scale(1.1) rotate(2deg);
}
.center-content {
  max-width: 1200px;
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  gap: 40px;
  flex-direction: row-reverse;
}

.text-content-home{
  max-width: 600px;
  flex: 1;
  text-align: center;
  font-size: 25px;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.7);
}

.portfolio-title{
  color: rgb(141, 255, 75);
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.7);
}

.text-content-projects{
  max-width: 600px;
  flex: 1;
  text-align: center;
  font-size: 25px;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.7);
}


.project-image {
  width: 400px;
  height: 400px;
  object-fit:contain;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.project-image:hover {
  transform: scale(1.1) rotate(2deg);
}


.projects-container {
  display: flex;
  flex-wrap: wrap;
  gap: 30px;
  justify-content: center;
}

.text-content-projects{
  width: 100%;
  text-align: center;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.7);
}

.projects-wrapper {
  padding: 25px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}


.mini-description{
  color: rgb(141, 255, 75);
  font-size: large;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.7);
}

.links {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  gap: 20px;
}

.links a {
  display: flex;
  align-items: center;
  color: #ffffff;
  text-decoration: none;
  transition: color 0.3s;
  font-size: 30px;
  padding-top: 10px;
}

.links a:hover {
  color: rgba(3, 255, 234, 0.514);
  transform: rotate(2deg);
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.footer {
  padding: 20px;
  font-size: 14px;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  background-color: rgba(15, 36, 3, 0.8);
}

.footer .osk {
  font-weight: bold;
  text-transform: lowercase;
  font-size: 20px;
}

.footer .copyright {
  margin-left: 10px;
  color: gray;
  font-size: 13px;
}

.footer .disclaimer{
  margin-left: 10px;
  color: gray;
  font-size: 13px;
}

@media (max-width: 600px) {
  .center-content .column-center {
    flex-direction: column;
    align-items: center;
    gap: 20px;
  }

  .text-content-home {
    text-align: center;
  }

  .home-image{
    width: 90%;
    height: auto;
  }
  .project-image {
    width: 90%;
    height: auto;
  }

  .projects-container {
    gap: 15px;
  }

  .icon-image {
    width: 40px;
    height: 40px;
  }

  .portfolio-title {
    font-size: 35px;
  }

  .portfolio-description {
    font-size: 16px;
  }

  .navbar {
    flex-direction: column;
    text-align: center;
  }

  .nav-links a {
    margin-right: 10px;
    font-size: 18px;

  }
}

@media (max-width: 900px) {
  .home-content {
    flex-direction: column;
    align-items: center;
    gap: 30px;
  }

  .projects-container {
    justify-content: center;
  }
}
